<template>
  <div id="app">
    <farm :collect.sync="collect" :basket.sync="basket" :chemical.sync="chemical" :pickaxe.sync="pickaxe"></farm>
    <name :hide-reclaim.sync="hideReclaim" :hide-mask.sync="hideMask"></name>
    <nav1 :hide-sence.sync="hideSence" :hide-center.sync="hideCenter" :hide-mask.sync="hideMask"></nav1>
    <menu :collect.sync="collect" :basket.sync="basket" :chemical.sync="chemical" :pickaxe.sync="pickaxe"></menu>
    <mask :hide-mask.sync="hideMask"></mask>
    <storehouse :hide-storehouse.sync="hideStorehouse" :hide-mask.sync="hideMask"></storehouse>
    <storehouse-icon :hide-storehouse.sync="hideStorehouse" :hide-mask.sync="hideMask"></storehouse-icon>
    <sence :hide-sence.sync="hideSence" :hide-mask.sync="hideMask"></sence>
    <reclaim :hide-reclaim.sync="hideReclaim" :hide-mask.sync="hideMask"></reclaim>
    <center :hide-center.sync="hideCenter" :hide-mask.sync="hideMask"></center>
</template>

<script>
import farm from './components/farm/index'
import name from './components/name/index'
import nav1 from './components/nav/index'
import menu from './components/menu/index'
import mask from './components/mask/index'
import storehouse from './components/storehouse/index'
import storehouseIcon from './components/storehouse-icon/index'
import sence from './components/sence/index'
import reclaim from './components/reclaim/index'
import center from './components/center/index'

export default {
  components: {
    farm,
    name,
    nav1,
    menu,
    mask,
    storehouse,
    storehouseIcon,
    sence,
    reclaim,
    center
  },
  data () {
    return {
      hideMask: true,
      hideStorehouse: true,
      hideSence: true,
      hideReclaim: true,
      hideCenter: true,
      collect: false,
      basket: false,
      chemical: false,
      pickaxe: false
    }
  }
}
</script>

<style>
@import url(./base/normalize.css);
@import url(./base/base.css);
@import url(./base/layout.css);
body{
  background: url(img/bg.jpg) no-repeat;
}
</style>



