<template>
  <nav class="nav">
    <ul>
      <li @click="showSence()" id="nav-sence">查看实景</li>
      <li>我要订餐</li>
      <li @click="showCenter()" id="nav-center">个人中心</li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: ['hideSence', 'hideCenter', 'hideMask'],
  methods: {
    showSence () {
      this.hideSence = !this.hideSence
      this.hideMask = !this.hideMask
    },
    showCenter () {
      this.hideCenter = !this.hideCenter
      this.hideMask = !this.hideMask
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.nav{
  position: absolute;
  right: 37px;
  top: 24px;
}
.nav ul{
  display: flex;
  align-items: flex-end;
}
.nav li{
  text-indent: -999em;
}
.nav li:nth-child(1){
  width: 85px;
  height: 112px;
  background-image: url(img/icon1.png?__sprite);
  margin-right: 13px;
}
.nav li:nth-child(2){
  width: 93px;
  height: 110px;
  background-image: url(img/icon2.png?__sprite);
  margin-right: 33px;
}
.nav li:nth-child(3){
  width: 85px;
  height: 112px;
  background-image: url(img/icon3.png?__sprite);
}
</style>



